.links {
  font-size: 0;
  &_dark {
    .links__link {
      .svg-icon {
        fill: black; } } } }

.links__item {
  display: inline-block;
  vertical-align: top;
  font-size: 14px;
  &:not(:last-child) {
    margin-right: 30px; } }

.links__link {
  display: block;
  opacity: 0.7;
  @include trans(opacity);
  text-decoration: none;
  color: inherit;
  .svg-icon {
    fill: white;
    display: block;
    font-size: 40px; }
  &:hover {
    opacity: 1; } }
